import {KeyboardPage} from "./pages/KeyboardPage";
import {TimescalePage} from "./pages/TimescalePage";

/**
 * Todas as pginas usadas para experimentos e ferramentas de desenvolvimento
 *
 * A classe deve possuir uma propriedade esttica `title`, manter o padro "<Group> - Name"
 */
export const PAGES = [
    KeyboardPage,
    TimescalePage
];

// Faz a ordenao dos itens
PAGES.sort((a: any, b: any) => {
    return a.title.localeCompare(b.title);
});
